body {
  min-height: 100vh;
  background: #4c4c4c url("https://unsplash.it/1500/900?image=1021") 0 0 / cover;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: sans-serif;
  display: flex;
}

.wrapper {
  width: 850px;
  display: flex;
}

video {
  box-shadow: 0 0 1px 3px rgba(0, 0, 0, .1);
}

.speed {
  background: #efefef;
  border-radius: 50px;
  flex: 1;
  align-items: flex-start;
  margin: 10px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 1px 3px rgba(0, 0, 0, .1);
}

.speed-bar {
  width: 100%;
  text-shadow: 1px 1px rgba(0, 0, 0, .2);
  color: #fff;
  height: 16.3%;
  background: linear-gradient(-170deg, #2376ae 0%, #c16ecf 100%);
  justify-content: center;
  align-items: center;
  padding: 2px;
  display: flex;
}

